import './FabricanteCambioModo.css'

import { IonAlert } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import { RouteComponentProps } from 'react-router'

import { Page } from '../../components'
import { RUTAS } from '../../constants'
import { cambiarModo, useToaster } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'

export const FabricanteCambioModo: React.FC<RouteComponentProps> = observer(({ history }) => {
  const rootStore = useStores()
  const { loginStore } = rootStore
  const [Alert, setAlert] = useState(false)
  const [TituloAlert, setTituloAlert] = useState('')
  const [TextoAlert, setTextoAlert] = useState('')
  const [FuncionAlert, setFuncionAlert] = useState('')
  const addToast = useToaster()

  return (
    <Page login_required fabricante={true} disableScroll={true} id={'cambio-modo-fabricante'}>
      <IonAlert
        isOpen={Alert}
        onDidDismiss={() => setAlert(false)}
        header={TituloAlert !== '' ? TituloAlert : 'Cerrar sesión'}
        message={TextoAlert !== '' ? TextoAlert : 'Confirma que desea cerrar sesión?'}
        buttons={[
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: () => {
              if (FuncionAlert === 'cambiar-usuario') {
                cambiarModo(rootStore, history, 10)
              } else if (FuncionAlert === 'cambiar-transportista') {
                cambiarModo(rootStore, history, 30)
              } else {
                history.replace(RUTAS.LOGOUT)
              }
            }
          }
        ]}
      />
      <div id="modos-wrapper" className='container-fluid'>
        <div className='modos-container'>
          <div className='py-2'>
            <div className='text-bold' style={{letterSpacing: 1.5}}>MODO DE USUARIO</div>
            ¿Cómo querés ver ABi?
          </div>

          <div className='row'>
            <a className='mb-3 col-12 col-xl-4' onClick={() => {
              setAlert(true)
              setTituloAlert('Cambiar a modo compras')
              setTextoAlert('Confirma que desea cambiar a modo compras?')
              setFuncionAlert('cambiar-usuario')
            }}>
              <img alt={'Modo tienda'} src={'assets/modo-tienda.jpg'}/>
            </a>

            <a className='mb-3 col-12 col-xl-4' onClick={() => addToast("Este modo es el activo actualmente")}>
              <img alt={'Modo proveedor'} src={'assets/modo-proveedor.jpg'}/>
            </a>

            <a className='mb-3 col-12 col-xl-4' onClick={() => {
              if (loginStore.transportista) {
                setAlert(true)
                setTituloAlert('Cambiar a modo driver')
                setTextoAlert('Confirma que desea cambiar a modo driver?')
                setFuncionAlert('cambiar-transportista')
              } else {
                let currentUrlParams = new URLSearchParams(window.location.search)
                currentUrlParams.set('registrot', '1')
                history.push(window.location.pathname + '?' + currentUrlParams.toString())
              }
            }
            }>
              <img alt={'Modo tienda'} src={'assets/modo-driver.jpg'}/>
            </a>
          </div>
        </div>
      </div>
    </Page>
  )
})

export default FabricanteCambioModo
